body{
  font-family: 'Nunito';
}

h1, h2, h3, h4, h5, h6 {
  font-family: catamaran, arial, sans-serif;
}

p, label, input {
  font-family: roboto, arial, sans-serif;
  font-size: 0.9em;
}

h1 {
  font-size: 1.5em;
}

h2 {
  font-size: 18px;
  padding: 10px 20px;
  color: #575ed8;
}

#chat {
  position: relative;
  min-height: 90vh;
  max-width: 500px;
  margin: 30px auto;
  background: #f9f9f9;
  border: 1px solid #ddd;
  box-shadow: 1px 3px 5px rgba(0,0,0,0.05);
  border-radius: 2px;

  .username-container {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background-color: white;
    margin: 0 20px;
    padding: 30px;
    opacity: 0;
    transition: all .4s ease-in-out;

    h1 {
      text-align: center;
    }

    input {
      background-color: #f9f9f9;
      font-size: 0.9em;
    }
  }

  &.locked {
    .messaging-container {
      opacity: 0;
      transition: all .4s ease-in-out;
    }

    .username-container {
      opacity: 1;
      transition: all .4s ease-in-out;
    }
  }
}

#window {
  height: calc(90vh - 84px);
  overflow: auto;
  background: #f9f9f9;
}

#output {
  p {
    padding: 14px 0px;
    margin: 0 20px;
    border-bottom: 1px solid #e9e9e9;
    color: #555;

    &.connected {
      color: #5AC18E;
    }

    &.disconnected {
      color: #FF6666;
    }
  }

  strong {
    color: #575ed8;
  }
}

#feedback {
  p {
    color: #aaa;
    padding: 15px 0px;
    margin: 0 20px;
  }
}

label {
  box-sizing: border-box;
  display: block;
  padding: 10px 20px;
}

input {
  padding: 10px 20px;
  box-sizing: border-box;
  background: #eee;
  border: 0;
  display: block;
  width: 100%;
  background: #fff;
  border-bottom: 1px solid #eee;
  font-size: 16px;
}

button {
  background: #575ed8;
  color: #fff;
  font-size: 18px;
  border: 0;
  padding: 12px 0;
  width: 100%;
  border-radius: 0 0 2px 2px;
}
